body {
  margin: 0;
  padding: 0;
  width: 100%;
  font-family: 'Quicksand', sans-serif;
}
html {
  box-sizing: border-box;
  font-size: 16px;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
.contenedor{
  width: 100%;
}
.header{
  width: 100%;
}
.contenedor-header{
  width:100%;
  display: flex;
  background: #453636;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.6rem;
  color: white;
}
@media screen and (max-width: 750px){
  .contenedor-header{
    font-size: 1.1rem;
  }
}